@import url(https://fonts.googleapis.com/css?family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font: 100% "Roboto", sans-serif;
  font-weight: normal; }

.content {
  margin-top: 10%; }

h1, h2, h3, h4, h5 {
  line-height: 1.2;
  font-weight: bold;
  letter-spacing: 2px;
  padding-top: 15px;
  margin-bottom: 0;
  color: #282c34 !important; }

h2 {
  font-size: 60px;
  line-height: 65px;
  font-weight: 800;
  text-transform: uppercase; }

div.header {
  padding-bottom: 5px; }

div h2.title {
  padding-bottom: 15px; }

div.body p {
  margin-top: 30px;
  font-size: 17px; }

div.body hr {
  height: 1px;
  margin-bottom: -1px;
  border: none;
  border-bottom-color: currentcolor;
  border-bottom-style: none;
  border-bottom-width: medium;
  border-bottom: 1px solid #ececec;
  margin-top: 40px; }

div.body h3 {
  font-size: 25px; }

div.body h4 {
  font-size: 20px; }

div.body h5 {
  font-size: 15px; }

div.body h6 {
  line-height: 1.2;
  font-weight: 700;
  letter-spacing: 2px;
  padding-top: 15px;
  font-size: 20px;
  color: #282c34 !important; }

div.body span.imgSubTitle {
  font-size: 18px;
  font-weight: 300;
  color: #6d6d6d;
  text-align: center; }

div.body a {
  text-decoration: none;
  color: #3886c9;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
  div.body a:hover {
    border-bottom-color: #1a1a1a;
    color: #0e5ca0; }

span.subTitle {
  font-size: 18px;
  font-weight: 300;
  color: #6d6d6d; }

.Diag {
  width: 75%;
  height: 75%;
  margin: 0 auto; }

.sideBarLink {
  text-decoration: none;
  color: #282c34; }
  .sideBarLink:hover {
    color: #6d6d6d; }
  .sideBarLink.active span {
    color: #282c34;
    font-weight: bold; }

li {
  padding: 10px 0px; }

p.taille {
  padding: 0px 50px; }

svg {
  box-sizing: content-box;
  box-sizing: initial; }

a {
  text-decoration: none;
  color: #ffff; }
  a.active {
    color: #3886c9; }
  a:hover {
    color: #438bca;
    -webkit-transition: 0.3s;
    transition: 0.3s; }

h6 {
  color: #3886c9;
  font-weight: 700 !important; }

